.web-aside {
  width: 2.49rem;
  border-right: 1px solid #eee;
  padding: 0.4rem 0 0.2rem 0;
  float: left;
}

.web-aside>h1 {
  display: flex;
  align-content: center;
  font-size: 0.3rem;
  margin-bottom: 0.2rem;
  padding-left: 0.4rem;
  font-weight: bold;
  color: #333;
}

.web-aside>h1 sup {
  border: 1px solid rgba(0,188,147,0.2);
  border-radius: 0.05rem;
  font-size: 0.12rem;
  color: #00bc93;
  font-weight: normal;
  display: block;
  height: 0.2rem;
  line-height: 0.2rem;
  padding: 0 0.05rem;
  margin-left: 0.1rem;
  background: rgba(0,188,147,0.1);
}

.web-aside ul li {
  display: block;
  padding-left: 0.4rem;
  margin: 0.05rem 0;
  height: 0.44rem;
  line-height: 0.44rem;
}

.web-aside ul li a {
  display: block;
  line-height: inherit;
  color: #666;
  text-decoration: none;
}

.web-aside ul li a:hover {
  color: #00bc93;
  cursor: pointer;
}

.web-aside ul li.group-item {
  padding-left: 0.55rem;
  color: #666;
  cursor: pointer;
}

.web-aside ul li.group-item span {
  color: #999;
  padding-left: 0.1rem;
  font-size: 0.12rem;
}

.web-aside ul li.group-item:hover span {
  color: #00bc93;
}

.web-aside ul li.active {
  background: rgba(0,188,147,0.1);
  border-right: 0.02rem solid #00bc93;
}

.web-aside ul li.active a,
.web-aside ul li.active span {
  color: #00bc93 !important;
}