.web-main {
  margin-left: 2.5rem;
  padding: 0.4rem 4.95rem 0.4rem 0.6rem;
  position: relative;
}

.web-main .markdown-content {
  min-width: 6rem;
}

.web-main .markdown-content h1 {
  font-size: 0.3rem;
  font-weight: bold;
  color: #333;
  line-height: 1.5;
  margin-bottom: 0.2rem;
}

.web-main .markdown-content h2 {
  font-size: 0.2rem;
  font-weight: bold;
  color: #333;
  line-height: 1.5;
  margin-bottom: 0.2rem;
}

.web-main .markdown-content p {
  font-size: 0.14rem;
  color: #666;
  line-height: 2.4;
  margin-bottom: 0.2rem;
}

.web-main .markdown-content p code {
  border: 1px dashed #ddd;
  background: #f7f7f7;
  margin: 0 0.04rem;
  padding: 0 0.04rem;
  border-radius: 0.03rem;
}

.web-main .markdown-content p em {
  font-style: italic;
  color: #333;
}

.web-main .markdown-content p strong {
  font-weight: bold;
  color: #333;
  padding: 0 0.04rem;
}

.web-main .markdown-content ul {
  margin-bottom: 0.2rem;
}

.web-main .markdown-content li {
  list-style-type: circle;
  margin-left: 1.5em;
  font-size: 0.14rem;
  color: #666;
  line-height: 1.5;
  margin-bottom: 0.1rem;
}

.web-main .markdown-content .code-box {
  border: 1px solid #ddd;
  position: relative;
  z-index: 2;
  border-radius: 0.05rem;
  overflow: hidden;
  margin-bottom: 0.4rem;
  cursor: pointer;
}

.web-main .markdown-content .code-box>header {
  padding: 0.15rem;
  border-bottom: 1px dashed #eee;
  font-size: 0.14rem;
  color: #333;
  font-weight: bold;
}

.web-main .markdown-content .code-box>.bd {
  height: 2rem;
  overflow: auto;
}

.web-main .markdown-content .code-box pre[class*='language-'] {
  border-radius: 0.05rem !important;
  margin: 0 !important;
  padding: 0.3rem !important;
  background: none !important;
}

.web-main .markdown-content .code-box:hover,
.web-main .markdown-content .code-box.active {
  border: 1px solid #00bc93;
  box-shadow: 0 0 0.1rem rgba(0,188,147,0.3);
}

.web-main .markdown-content .code-box.active>.bd {
  height: auto;
}

.web-main .markdown-content table {
  border-collapse: collapse;
  width: 100%;
  margin-right: 4rem;
  margin-bottom: 0.3rem;
}

.web-main .markdown-content table th,
.web-main .markdown-content table td {
  border: 1px solid #ddd;
  padding: 0.12rem 0.16rem;
  font-size: 0.14rem;
  line-height: 1.5;
  color: #666;
}

.web-main .markdown-content table thead th {
  background: #f7f7f7;
  color: #333;
  font-weight: bold;
}

.web-main .markdown-content a {
  color: #00bc93;
}

.web-main .mobile-preview {
  position: fixed;
  z-index: 9;
  right: 0.6rem;
  top: 50%;
  margin-top: -3.7rem;
}