.web-aside {
  width: 2.49rem;
  border-right: 1px solid #eee;
  padding: 0.4rem 0 0.2rem 0;
  float: left;
}

.web-aside>h1 {
  display: flex;
  align-content: center;
  font-size: 0.3rem;
  margin-bottom: 0.2rem;
  padding-left: 0.4rem;
  font-weight: bold;
  color: #333;
}

.web-aside>h1 sup {
  border: 1px solid rgba(0,188,147,0.2);
  border-radius: 0.05rem;
  font-size: 0.12rem;
  color: #00bc93;
  font-weight: normal;
  display: block;
  height: 0.2rem;
  line-height: 0.2rem;
  padding: 0 0.05rem;
  margin-left: 0.1rem;
  background: rgba(0,188,147,0.1);
}

.web-aside ul li {
  display: block;
  padding-left: 0.4rem;
  margin: 0.05rem 0;
  height: 0.44rem;
  line-height: 0.44rem;
}

.web-aside ul li a {
  display: block;
  line-height: inherit;
  color: #666;
  text-decoration: none;
}

.web-aside ul li a:hover {
  color: #00bc93;
  cursor: pointer;
}

.web-aside ul li.group-item {
  padding-left: 0.55rem;
  color: #666;
  cursor: pointer;
}

.web-aside ul li.group-item span {
  color: #999;
  padding-left: 0.1rem;
  font-size: 0.12rem;
}

.web-aside ul li.group-item:hover span {
  color: #00bc93;
}

.web-aside ul li.active {
  background: rgba(0,188,147,0.1);
  border-right: 0.02rem solid #00bc93;
}

.web-aside ul li.active a,
.web-aside ul li.active span {
  color: #00bc93 !important;
}
.web-main {
  margin-left: 2.5rem;
  padding: 0.4rem 4.95rem 0.4rem 0.6rem;
  position: relative;
}

.web-main .markdown-content {
  min-width: 6rem;
}

.web-main .markdown-content h1 {
  font-size: 0.3rem;
  font-weight: bold;
  color: #333;
  line-height: 1.5;
  margin-bottom: 0.2rem;
}

.web-main .markdown-content h2 {
  font-size: 0.2rem;
  font-weight: bold;
  color: #333;
  line-height: 1.5;
  margin-bottom: 0.2rem;
}

.web-main .markdown-content p {
  font-size: 0.14rem;
  color: #666;
  line-height: 2.4;
  margin-bottom: 0.2rem;
}

.web-main .markdown-content p code {
  border: 1px dashed #ddd;
  background: #f7f7f7;
  margin: 0 0.04rem;
  padding: 0 0.04rem;
  border-radius: 0.03rem;
}

.web-main .markdown-content p em {
  font-style: italic;
  color: #333;
}

.web-main .markdown-content p strong {
  font-weight: bold;
  color: #333;
  padding: 0 0.04rem;
}

.web-main .markdown-content ul {
  margin-bottom: 0.2rem;
}

.web-main .markdown-content li {
  list-style-type: circle;
  margin-left: 1.5em;
  font-size: 0.14rem;
  color: #666;
  line-height: 1.5;
  margin-bottom: 0.1rem;
}

.web-main .markdown-content .code-box {
  border: 1px solid #ddd;
  position: relative;
  z-index: 2;
  border-radius: 0.05rem;
  overflow: hidden;
  margin-bottom: 0.4rem;
  cursor: pointer;
}

.web-main .markdown-content .code-box>header {
  padding: 0.15rem;
  border-bottom: 1px dashed #eee;
  font-size: 0.14rem;
  color: #333;
  font-weight: bold;
}

.web-main .markdown-content .code-box>.bd {
  height: 2rem;
  overflow: auto;
}

.web-main .markdown-content .code-box pre[class*='language-'] {
  border-radius: 0.05rem !important;
  margin: 0 !important;
  padding: 0.3rem !important;
  background: none !important;
}

.web-main .markdown-content .code-box:hover,
.web-main .markdown-content .code-box.active {
  border: 1px solid #00bc93;
  box-shadow: 0 0 0.1rem rgba(0,188,147,0.3);
}

.web-main .markdown-content .code-box.active>.bd {
  height: auto;
}

.web-main .markdown-content table {
  border-collapse: collapse;
  width: 100%;
  margin-right: 4rem;
  margin-bottom: 0.3rem;
}

.web-main .markdown-content table th,
.web-main .markdown-content table td {
  border: 1px solid #ddd;
  padding: 0.12rem 0.16rem;
  font-size: 0.14rem;
  line-height: 1.5;
  color: #666;
}

.web-main .markdown-content table thead th {
  background: #f7f7f7;
  color: #333;
  font-weight: bold;
}

.web-main .markdown-content a {
  color: #00bc93;
}

.web-main .mobile-preview {
  position: fixed;
  z-index: 9;
  right: 0.6rem;
  top: 50%;
  margin-top: -3.7rem;
}
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
